import React, { useEffect, useState } from 'react';

import { Layout, PrimaryButton } from '@daimler/react-ux-business-app';

import { useBack } from '@hooks/useBack';

import { Sider as SiderContent } from './components/Sider';
import { Content } from './components/Content';

import { PageHeader, PageSider, PageContent } from './styled';
import { Path } from '@router/types';
import { Trans, useTranslation } from '@i10n/index';
import { useParams } from 'react-router-dom';
import { apiClient } from '@api/client';
import { Invoice } from '@api/generated/apiClient.generated';

type InvoiceDomainProps = {};

export const InvoiceDomain: React.FC<InvoiceDomainProps> = () => {
  const handleBack = useBack(Path.INVOICES);
  const t = useTranslation();

  const { id } = useParams();

  const [invoice, setInvoice] = useState<Invoice>();
  const [currentId, setCurrentId] = useState<number>(
    Number.parseInt(id ?? '-1'),
  );

  useEffect(() => {
    apiClient.invoices(currentId).then((m) => setInvoice(m));
  }, [currentId]);

  const onClick = (id: number) => {
    setCurrentId(id);
  };

  return (
    <Layout>
      <PageHeader>
        <PrimaryButton onClick={handleBack}>
          {t('Rabs.Common.Back')}
        </PrimaryButton>
      </PageHeader>
      <Layout>
        <PageSider>
          <SiderContent currentId={currentId} onClick={onClick} />
        </PageSider>
        <PageContent>
          <Content invoice={invoice} />
        </PageContent>
      </Layout>
    </Layout>
  );
};
