import { apiClient } from '@api/client';
import {
  InvoiceSearchRequest,
  SearchInvoiceItem,
} from '@api/generated/apiClient.generated';
import { useTranslation } from '@i10n/index';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import {
  SiderWrapper,
  ListItem,
  Status,
  InvoiceNumberWrapper,
  CustomerNumberWrapper,
} from './styled';

type SiderProps = {
  currentId: number;
  onClick: (currentId: number) => void;
};

export const Sider: React.FC<SiderProps> = ({ currentId, onClick }) => {
  const t = useTranslation();
  const { filter } = useParams();
  const [invoices, setInvoices] = useState<SearchInvoiceItem[]>([]);

  useEffect(() => {
    const request = JSON.parse(filter ?? '{}') as InvoiceSearchRequest;
    apiClient.invoicesSearch(request).then((r) => setInvoices(r.items ?? []));
  }, []);

  return (
    <SiderWrapper>
      {invoices.map((item, i) => {
        return (
          <ListItem
            key={i}
            active={item.id === currentId}
            onClick={() => onClick(item.id)}
          >
            <InvoiceNumberWrapper>
              <Status success={!item.isFailed} />
              {`${item.id} - ${t(
                `Rabs.Invoices.Filter.InvoiceTypes.${item.invoiceType}`,
              )}`}
            </InvoiceNumberWrapper>
            <CustomerNumberWrapper>
              {item.embossedId} ({item.licensePlate})
            </CustomerNumberWrapper>
          </ListItem>
        );
      })}
    </SiderWrapper>
  );
};
